// ==========================================================================
// AWARDS
// ==========================================================================

@include new("awards") {
    position: relative;

    &:before {
        @include reset-pseudo(rgba($black, 0.1));
        @include reset-full-scren;
    }

    .swiper-container {
        // full screen slider (overflow)
        overflow: visible;
        margin: 4rem 0;
    }

    .swiper-wrapper {
        pointer-events: none;
    }

    .swiper-container-horizontal {
        > .swiper-scrollbar {
            display: none;
            position: absolute;
            left: 1.5%;
            bottom: -50%;
            width: 100%;
            height: 1px;
            border-radius: 0;
            background: rgba(0,0,0,0.2);

            @media(max-width: $max-width-4casas) {
                display: block;
            }
        }

        .swiper-scrollbar-drag {
            top: -3px;
            height: 7px;
            border-radius: 0;
            background: $complementary-color;
        }
    }

    .swiper-slide {
        position: relative;
        // reset width
        // width: 25%;
        // forze same height items
        height: auto;
        // outside elements opacity TRICK
        opacity: 0.3;

        padding-left: 4rem;
        transition: all 0.3s ease-out;

        &.destroy {
            border: 1px solid red;
        }

        @media(max-width: $max-width-4casas) {
            padding-left: 0;
            transition: all 0s ease-out;
        }

        // focus element opacity
        &-active,
        &-next,
        &-next + .swiper-slide {
            opacity: 1;
            pointer-events: auto;
        }

        @media(max-width: $max-width-4casas) {
            &-prev,
            &-next,
            &-next + .swiper-slide {
                opacity: 0.3;
                pointer-events: none;
            }
        }
    }

    @include has("wrapper") {
        @include reset-figure;
        width: 100%;
    }

    @include has("logo") {
        width: auto;
        height: 60px;
        object-fit: cover;
        margin-bottom: 1rem;

        @media(max-width: $max-width-4casas) {
            height: 50px;
        }
    }

    @include has("title") {
        @include fontSet(16px, 20px);
        font-weight: bold;
        color: $black;

        @media(max-width: $max-width-4casas) {
            @include fontSet(12px, 14px);
        }
    }

    @include has("date") {
        margin-top: 0.5rem;
        @include fontSet(16px, 20px);
        color: rgba($black, 0.6);

        @media(max-width: $max-width-4casas) {
            @include fontSet(12px, 14px);
        }
    }
}
